body {
  font: 100% Helvetica, sans-serif;
  color: #333;
  margin: 0;
}

.btn-blue {
  color: white;
  border: none;
  font-weight: 400;
  padding: 5px 10px;
  background-color: #25f;
  transition: filter 50ms ease-in-out;
}

.btn-blue:hover {
  filter: sepia(30%);
}

.btn-blue:active {
  filter: sepia(90%);
}

.bg-blue {
  background-color: #25f;
}

